@import "Utilities";

.character-wrapper {
  @media (min-width: $desktop) {
    display: flex;
    width: 80%;
    margin: auto;
  }
  .character-details-container {
    background-color: $sectionBGColor;

    margin: 10px;
    height: 310px;
    border: 17px solid transparent;
    border-image: $dndBorder 116;
    @media (min-width: $desktop) {
      width: 45%;
      margin-inline: auto;
    }
    .character-details {
      display: grid;
      grid-template-columns: 1fr 1fr;
      justify-items: center;
      align-items: center;
      label {
        text-decoration: underline;
      }
      .detail {
        display: flex;

        gap: 4px;
        padding: 5px;
        font-size: 19px;
      }
      .level {
        text-decoration: underline;
        grid-row-start: 1;
        grid-column-start: 2;
        font-size: 19px;
      }
      .lvl-up-btn {
        font-size: 16px;
        grid-column-start: 2;
        font-weight: bold;
      }
      .skills-tab {
        font-size: 17px;
        grid-row-start: 2;
        @media (min-width: $desktop) {
          display: none;
        }
      }
    }

    .level-up-tab-container {
      position: absolute;
      display: flex;
      flex-direction: column;
      top: 65px;
      left: 5px;
      right: 5px;
      z-index: 4;
      margin-inline: auto;
      min-height: 250px;
      max-width: 380px;
      background-color: $sectionBGColor;
      border: 17px solid transparent;
      border-image: $dndBorder 116;
      .new-health-container {
        .hit-dice {
          text-decoration: underline;
          padding-bottom: 10px;
        }
        .new-health-input {
          margin-inline: 5px;
          width: 35px;
          font-size: 20px;
        }
      }
      .new-features-container {
        padding: 5px;
        margin-top: 10px;
        border-block: 2px solid;
        border-bottom: 1px solid #808080d9;
        box-shadow: 0px 1px 2px 2px rgba(117, 117, 117, 0.91);
        background-color: $backgroundColor;
        .perk {
          padding-block: 4px;
          p {
            font-family: $parFont;
            font-size: 18px;
          }
        }

        .item-value,
        .perk_prof_bonus {
          display: flex;
          p {
            padding-left: 5px;
          }
        }
        .perk_features,
        .perk_spellcasting,
        .perk_class_specific {
          border-block: 1px solid $fadedBorderColor;
        }
      }
      .save-btn {
        margin-top: 15px;
        font-size: 18px;
        font-weight: bold;
      }
      .close-btn {
        align-self: flex-end;
        font-size: 18px;
        background-color: lightgrey;
        width: 30px;
        height: 28px;
        font-size: 25px;
        text-align: center;
        border: 1px solid;
        border-radius: 7px;
        margin: 0 5px 10px;
      }
    }
    .health-wrapper {
      display: flex;
      justify-content: center;
      flex-direction: column;
      align-items: center;
      border-top: 1px solid;
      border-radius: 20%;
      margin-top: 9px;
      padding-top: 9px;
      header {
        text-decoration: underline;
      }

      .temp-health {
        display: flex;
        justify-content: center;
        align-items: center;
        border: 2px solid;
        width: 70px;
        height: 38px;
        margin-bottom: 5px;
        background-color: $backgroundColor;
        input {
          width: 67px;
          font-size: 28px;
          border: none;
        }
      }

      .to-max-health {
        margin-top: 15px;

        button {
          margin-inline: 8px;
          font-size: 19px;
          font-weight: bold;
          padding: 3px;
          height: 31px;
        }
      }

      .health-container {
        display: flex;
        align-items: center;

        button {
          font-size: 27px;
          font-weight: bold;
          margin-inline: 8px;
          padding: 1px;
          height: 35px;
          width: 35px;
        }
        .health {
          display: flex;
          align-items: center;
          font-size: 20px;
          border: 1px solid;
          border-radius: 3px 15px 3px 15px;
          padding: 5px;
          margin-inline: 8px;
          text-align: center;
          background-color: $backgroundColor;

          h4 {
            height: 23px;
          }

          input {
            width: 70px;
            border-bottom: none;
            padding-top: 6px;
            font-size: 20px;
            font-weight: bold;
          }
        }
      }
    }
  }
  .show-character-info-btn {
    display: none;
    @media (max-width: $desktop) {
      display: block;
      position: fixed;
      left: calc(100% - 45px);
      top: 65px;
      z-index: 3;
      font-size: 26px;
      height: 37px;
      width: 37px;
      padding-top: 4px;
      border-radius: 30%;
      background-color: #c7c7c7a6;
    }
  }
}
